}

.flex-control-paging li a {
  display: block;
  width: 11px;
  height: 11px;
  cursor: pointer;
  text-indent: -9999px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border-radius: 20px;
  background: #666;
